/*
 * @Author: SiYuan Wang
 * @Date: 2018-11-26 08:58:23
 * @Description: toast styles for fc
 */
.fc-toast {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2002;
  opacity: 0;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  -webkit-transform: translate3d(0, 100%, 0) scale(0.5);
  -moz-transform: translate3d(0, 100%, 0) scale(0.5);
  -ms-transform: translate3d(0, 100%, 0) scale(0.5);
  -o-transform: translate3d(0, 100%, 0) scale(0.5);
  transform: translate3d(0, 100%, 0) scale(0.5);
  pointer-events: none; }
  .fc-toast.fc-toast__success .fc-toast--content {
    background-color: rgba(0, 150, 136, 0.8); }
  .fc-toast.fc-toast__error .fc-toast--content {
    background-color: rgba(233, 39, 88, 0.9); }
  .fc-toast.fc-toast__info .fc-toast--content {
    background-color: rgba(0, 0, 0, 0.7); }
  .fc-toast.fc-toast__active {
    opacity: 1;
    -webkit-transform: translate3d(0, -120px, 0) scale(1);
    -moz-transform: translate3d(0, -120px, 0) scale(1);
    -ms-transform: translate3d(0, -120px, 0) scale(1);
    -o-transform: translate3d(0, -120px, 0) scale(1);
    transform: translate3d(0, -120px, 0) scale(1); }

.fc-toast--box {
  padding: 0 30px;
  line-height: 0;
  text-align: center; }

.fc-toast--content {
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  display: inline-block;
  padding: 10px 20px;
  background-color: rgba(0, 0, 0, 0.7);
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  border-radius: 30px;
  line-height: 20px;
  color: #FFFFFF;
  font-size: 14px;
  text-align: center;
  vertical-align: top; }

/*
 * @Author: SiYuan Wang
 * @Date: 2018-11-26 09:19:39
 * @Description: loading styles for fc
 */
.fc-loading {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2001; }

.fc-loading-container,
.fc-loading--container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: -1px;
  z-index: 200;
  background-color: rgba(255, 255, 255, 0.6);
  text-align: center; }

.fc-loading-box,
.fc-loading--box {
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -10px 0 0 -21px;
  width: 42px;
  height: 20px;
  line-height: 20px;
  color: #999999;
  text-align: center; }

.fc-loading-item,
.fc-loading--item {
  display: inline-block;
  position: absolute;
  top: 4px;
  left: 0;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  background-color: #FF3333;
  transform: translateX(0);
  animation: load 1.04s linear infinite; }
  .fc-loading-item:nth-child(2),
  .fc-loading--item:nth-child(2) {
    transform: translateX(30px);
    opacity: .5;
    background-color: #2B81EB;
    animation: load2 1.04s linear infinite; }

@keyframes load {
  0% {
    opacity: 0.8;
    transform: translateX(0) scale(1); }
  25% {
    transform: translateX(15px) scale(1.5); }
  50% {
    opacity: 0.8;
    transform: translateX(30px) scale(1); }
  75% {
    transform: translateX(15px) scale(0.5); }
  100% {
    opacity: 0.5;
    transform: translateX(0) scale(1); } }

@keyframes load2 {
  0% {
    transform: translateX(30px) scale(1); }
  25% {
    transform: translateX(15px) scale(0.5); }
  50% {
    opacity: 0.5;
    transform: translateX(0) scale(1); }
  75% {
    opacity: 0.8;
    transform: translateX(15px) scale(1.5); }
  100% {
    opacity: .5;
    transform: translateX(30px) scale(1); } }

